.container {
  margin: .6rem 2rem;
  cursor: pointer;
  display: flex;

  img {
    width: 300px;
    height: 225px;
  }

  div {
    margin-left: 2rem;
    align-self: center;

    h1 {
      width: 500px;
      margin: .5rem 0;
      font-size: 1.3rem;
      font-weight: 500;
    }
    p {
      margin: 1rem 0;
      max-width: 500px;
      color: rgba(0,0,0,.8);
      font-size: .8rem;
    }
  }
}

.image {
  margin: 0;
  padding: 0;
  width: 300px;
  height: 225px;
}

@media all and (max-width: 780px) {

  .container {
    flex-direction: column;
    margin: .5rem 2rem;
    max-width: 90vw;

    div {
      margin: .4rem 0;

      h1 {
        max-width: 300px;
        text-align: center;
      }

      p {
        overflow: hidden;
        max-width: 300px;
      }

      p:first-of-type {
        text-align: center;
      }

    }
  }



}
